import React, { Component } from 'react';
import { Link } from 'react-router-dom';

const links = [
  { fa: "codepen", to: "https://codepen.io/sid7/", title: "Codepen"},
  { fa: "github", to: "https://github.com/sid7", title: "GitHub" },
  { fa: "free-code-camp", to: "https://www.freecodecamp.org/sid7", title: "Free Code Camp" },
  { fa: "envelope-o", to: "mailto:suyashsidzzz@gmail.com", title: "E-mail"}
];

class Home extends Component {
  componentDidMount() {
    document.querySelector('meta[name=theme-color]').setAttribute('content', "#673AB7");
  }
  render() {
    return (
      <div id="home" className="container sec">
        <div className="row">
          <div className="col-12 sec-header">
            <a className="sec-hlink" href={window.location.href}>Suyash</a>
          </div>
        </div>
        <div className="row sec-links mt-4">
          <div className="col">
              <Link to="/about" className="btn sec-link sec-for-about btn-block">About Me</Link>
          </div>
          <div className="col">
            <Link to="/portfolio" className="btn sec-link sec-for-portfolio btn-block">My Portfolio</Link>
          </div>
          <div className="col">
            <Link to="/contact" className="btn sec-link sec-for-contact btn-block">Contact Me</Link>
          </div>
        </div>
        <div className="row mt-4 text-center">
          <div className="col-12 social-links">
            {
              links.map(({fa,to,title}, i) => {
                return (
                  <a key={`sec-home-link-${i}`} rel="noreferrer noopener" href={to} target="_blank">
                    <i className={`fa fa-${fa} fa-3x px-1`} title={title}></i>
                  </a>
                );
              })
            }
          </div>
        </div>
      </div>
    );
  }
}

export default Home;



