import React, { Component } from 'react';

class Modal extends Component {
  email() {
    return (
      <div className="row">
        <div className="col">
          <form className="form">
            <div className="form-group">
              <label htmlFor="senderName">Name</label>
              <input type="text" className="form-control" name="senderName" />
            </div>
            <div className="form-group">
              <label htmlFor="email">E-mail</label>
              <input type="text" className="form-control" name="email" placeholder="someone@from.com" />
            </div>
            <div className="form-group">
              <textarea className="form-control" placeholder="Write hare..."></textarea>
            </div>
            <button type="button" className="btn btn-sec"><i className="fa fa-send"></i> Send</button>
          </form>
        </div>
      </div>
    );
  }
  projectsInfo() {
    let {info} = this.props;
    return (
      <div className="row">
        <div className="col-md-5">
          <figure className="figure bg-light p-1">
            <img className="figure-img img-fluid" src={info.images.large} alt={`Screenshot of ${info.title}`} />
            <figcaption className="figure-caption text-center">Screenshot</figcaption>
          </figure>
        </div>
        <div className="col-md-7">
          <table className="table table-bordered">
            <tbody>
              <tr>
                <td>Title</td><td>{info.title}</td>
              </tr>
              <tr>
                <td>About</td><td>{window.$(info.details).text()}</td>
              </tr>
              <tr>
                <td>links</td>
                <td>
                  <a rel="noreferrer noopener" href={`https://codepen.io/sid7/pen/${info.id}`} target="_blank" className="btn btn-sec btn-sm">Code</a>{' '}
                  <a rel="noreferrer noopener" href={`https://codepen.io/sid7/full/${info.id}`} target="_blank" className="btn btn-sec btn-sm">View</a>{' '}
                </td>
              </tr>
              <tr>
                <td>love</td>
                <td>{info.loves === "Hearts" ? 0 : info.loves}</td>
              </tr>
              {
                ['views', 'comments'].map(obj => {
                  window.pinfo = info[obj];
                  return (<tr key={obj}><td>{obj}</td><td>{info[obj]}</td></tr>);
                })
              }
            </tbody>
          </table>
        </div>
      </div>
    );
  }
  render() {
    return (
      <div id="modal" className="modal fade sec-for-portfolio" tabIndex="-1" role="dialog">
        <div className="modal-dialog modal-lg" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" dangerouslySetInnerHTML={{__html: this.props.info ? this.props.info.title : "Suyash"}}></h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body pinfo" style={{backgroundImage: `linear-gradient(rgba(255,255,255,.4),rgba(255,255,255,.4)),url(${this.props.info ? this.props.info.images.large : null})`}}>
              <div className="container-fluid">
                {(this.props.info) ? this.projectsInfo() : null}
              </div>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-outline-danger" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Modal;



