import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class page404 extends Component {
  componentDidMount() {
    document.querySelector('meta[name=theme-color]').setAttribute('content', '#673AB7');
  }
  render() {
    return (
      <div id="page404" className="container-fluid sec text-center">
        <div className="row">
          <div className="col-sm-12 offset-xl-3 col-xl-6">
            <div className="card animated pulse">
              <div className="card-header">404</div>
              <div className="card-body">Page Not Found</div>
              <div className="row card-footer">
                <div className="col">
                  <button type="button" className="btn btn-sec btn-lg btn-block" onClick={()=>this.props.history.goBack()}><i className="fa fa-undo fa-fw"></i> Go to Previous Page</button>
                </div>
                <div className="col">
                  <Link to="/" className="btn btn-sec btn-lg btn-block"><i className="fa fa-home fa-fw"></i> Go to Home Page</Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default page404;



