import React, { Component } from 'react';
import { Link } from 'react-router-dom';

const links = [
  { fa: "codepen", to: "https://codepen.io/sid7/", text: "Codepen" },
  { fa: "github", to: "https://github.com/sid7", text: "GitHub" },
  { fa: "free-code-camp", to: "https://www.freecodecamp.org/sid7", text: "Free Code Camp" }
];
const Slinks = [
  { fa: "facebook", to: "https://www.facebook.com/sidzxzx", text: "Facebook" },
  { fa: "linkedin", to: "https://www.linkedin.com/in/suyashbhutani/", text: "LinkedIn" },
  { fa: "twitter", to: "https://twitter.com/sidzxzx", text: "twitter" }
];
const Slink = ({fa, to, text}) => {
  return (
    <a rel="noreferrer noopener" className="btn btn-sec btn-lg btn-block" href={to} target="_blank">
        <i className={`fa fa-${fa} fa-fw`}></i> {text}</a>
  );
};

class Contact extends Component {
  componentDidMount() {
    document.querySelector('meta[name=theme-color]').setAttribute('content', "#E91E63");
  }
  render() {
    return (
      <div id="contact" className="container-fluid sec">
        <div className="row sec-head">
          <div className="col-1 text-center">
            <Link to="/">&#171;</Link>
          </div>
          <div className="col-11">
            <a href="/contact">Contact Me</a>
          </div>
        </div>
        <div className="row sec-content">
          <div className="col-12">
            <div className="row pb-4" style={{borderBottom: "2px solid"}}>
              <div className="col-12 mt-4">
                <h2 className="display-4">Links -</h2>
              </div>
              {
                links.map((props) => <div key={`sl-${props.text}`} className="col mt-2"><Slink {...props} /></div>)
              }
            </div>
            <div className="row pb-4" style={{borderBottom: "2px solid"}}>
              <div className="col-12 mt-4">
                <h2 className="display-4">Social Links -</h2>
              </div>
              {
                Slinks.map((props) => {
                  return <div key={`sl-${props.text}`} className="col mt-2"><Slink {...props} /></div>
                })
              }
            </div>
            <div className="row">
              <div className="col-12 mt-4">
                <h2 className="display-4">E-Mail -</h2>
              </div>
              <div className="col-12">
                <a rel="noreferrer noopener" href="mailto:suyashsidzzz@gmail.com" className="btn btn-sec" target="_blank"><i className="fa fa-envelope fa-fw"></i> E-Mail</a>{' '}
                &nbsp;<p className="h3 text-muted d-inline font-weight-light">suyashsidzzz@gmail.com</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;



