import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class About extends Component {
  componentDidMount() {
    document.querySelector('meta[name=theme-color]').setAttribute('content', "#03A9F4");
  }
  render() {
    return (
      <div id="about" className="container-fluid sec">
        <div className="row sec-head">
          <div className="col-1 text-center">
            <Link to="/">&#171;</Link>
          </div>
          <div className="col-11">
            <a href="/about">About Me</a>
          </div>
        </div>
        <div className="row sec-content">
          <div className="col-12 text-center">
            <h1 className="sec-hlink mt-4" style={{fontSize: "25vmin"}}>Coming Soon.. </h1>
          </div>
        </div>
      </div>
    );
  }
}

export default About;



