import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import 'animate.css';

const Card = (props) => {
  const { id, images, title, details, cardInfo } = props;
  return (
    <div id={id} className="col-lg-4">
      <div className="pen-card" tabIndex="-1">
        <div className="img-container">
          <img src={images.large} className="img-fluid" alt={`Screenshot of ${title}`} />
          <div className="img-text">
            <span className="text-title">{title}</span>
            <span className="text">{window.$(details).text()}</span>
          </div>
        </div>
        <div className="actions">
          <a rel="noreferrer noopener" href={`https://www.codepen.io/sid7/pen/${id}/`} className="btn btn-sec" target="_blank">Code</a>{' '}
          <a rel="noreferrer noopener" href={`https://www.codepen.io/sid7/full/${id}/`} className="btn btn-sec" target="_blank">View</a>{' '}
          <button type="button" className="btn btn-sec float-right" onClick={()=>cardInfo(props)} data-toggle="modal" data-target="#modal">Info</button>
        </div>
      </div>
    </div>
  );
};
const Loading = () => {
  return (
    <div className="col-12 loading">
      <i className="fa fa-cog fa-spin"></i>
      <h1 className="display-1">Loading..</h1>
    </div>
  );
}

class Portfolio extends Component {
  constructor() {
    super();
    this.cardInfo = this.cardInfo.bind(this);
  }
  cardInfo(modalInfo) {
    window.modalInfo = modalInfo;
    this.props.configModal({ modalInfo, show: true });
  }
  getCards() {
    return this.props.projects.map(pen => {
      return <Card key={pen.id} {...pen} cardInfo={this.cardInfo} />
    });
  }
  componentDidMount() {
    let $ = window.$;
    $('body').on('mousemove', '.pen-card img', function(e) {
      let origin = ((e.pageX - $(this).offset().left) / $(this).width()) * 100 + '% ' + ((e.pageY - $(this).offset().top) / $(this).height()) * 100 +'%';
      $(this).css({'transform-origin': origin});
    });
    $('.pinfo').on('mousemove', function(e) {
      let origin = ((e.pageX - $(this).offset().left) / $(this).width()) * 100 + '% ' + ((e.pageY - $(this).offset().top) / $(this).height()) * 100 +'%';
      $(this).css({ 'background-position': origin });
    });
    document.querySelector('meta[name=theme-color]').setAttribute('content', "#20c997");
    $('.pen-card').addClass('animated zoomIn').one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function() {
      $(this).removeClass('animated zoomIn');
    });
  }
  render() {
    return (
      <div id="portfolio" className="container-fluid sec">
        <div className="row sec-head">
          <div className="col-1 text-center">
            <Link to="/">&#171;</Link>
          </div>
          <div className="col-11 col-md-7">
            <a href="/portfolio">My Portfolio</a>{' '}
          </div>
          <div className="col-4 text-right d-none d-md-block">
            <Link to="/portfolio/slideShow" className="btn btn-sec" style={{margin: "4vmin 0"}}><i className="fa fa-television fa-lg"></i> Slide Show</Link>
          </div>
        </div>
        <div className="row sec-cards">
          {this.props.loading ? <Loading /> : this.getCards()}
        </div>
      </div>
    );
  }
}

export default Portfolio;



