import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery/src/jquery';
import './App.css';
import Home from './home.js';
import About from './about.js';
import Portfolio from './portfolio.js';
import Contact from './contact.js';
import 'bootstrap';
import page404 from './page-not-found-404';
import ShowTime from './showtime.js';
import Modal from './modal.js';

function addOnLoad() {
  window.$.fn.extend({
    animateCss: function (animationName, callback) {
      var animationEnd = 'webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend';
      this.addClass('animated ' + animationName).one(animationEnd, function() {
        $(this).removeClass('animated ' + animationName);
        if (callback) {
          callback();
        }
      });
      return this;
    }
  });
  $('.social-links a .fa,.sec-links a').animateCss('zoomIn');
}
var isaddOnLoad = false;
class App extends Component {
  constructor() {
    super();
    this.state = {
      loading: true,
      myProjects: [],
      modalInfo: null,
      slide: 0
    }
    this.configModal = this.configModal.bind(this);
  }
  getPenData() {
    let page = 1, pens = [];
    const fetchData = (callback) => {
      const url =`https://cpv2api.com/pens/public/sid7?page=${page}`;
      fetch(url)
      .then(res => {
        res.json().then(json => {
          if(json.data) {
            pens.push(...json.data);
            page++;
            return fetchData(callback);
          }
          return callback();
        });
      });
    }
    fetchData(() => {
      pens.forEach(a => {
        ['large', 'small'].forEach(b => {
          a.images[b] = a.images[b].replace(/^http:\/\//i, 'https://');
        });
      });
      this.setState({loading: false, myProjects: pens});
    });
  }
  configModal({modalInfo, show}) {
    if(modalInfo) {
      this.setState({modalInfo});
    }
    if(show === true) {
      // $('#modal').modal();
    }
  }
  componentDidMount() {
    this.getPenData();
    if(!isaddOnLoad) {
      addOnLoad();
      isaddOnLoad = true;
    }
  }
  render() {
    return [
      <BrowserRouter key="Router-1" basename={process.env.PUBLIC_URL}>
        <Switch>
          { ['/', '/index.html'].map(a => <Route key={`path-${a}`} exact path={a} component={Home} />) }
          <Route path="/about" component={About} />
          <Route path="/portfolio/slideShow/" render={(props) => <ShowTime {...this.state} {...props} />} />
          <Route path="/portfolio" render={() => <Portfolio loading={this.state.loading} projects={this.state.myProjects} configModal={this.configModal} />} />
          <Route path="/contact" component={Contact} />
          <Route component={page404} />
        </Switch>
      </BrowserRouter>,
      <Modal key="modal-1" projects={this.state.myProjects} info={this.state.modalInfo} />
    ];
  }
}

export default App;



