import React, { Component } from 'react';
import { Link } from 'react-router-dom';

const Loading = () => {
  return (
    <div className="col-12 loading">
      <i className="fa fa-cog fa-spin"></i>
      <h1 className="display-1">Loading..</h1>
    </div>
  );
}

class ShowTime extends Component {
  getItems() {
    const $ = window.$;
    let { myProjects, slide } = this.props;
    let items = myProjects.map((item, i) => {
      return (
        <div key={`item-${i}`} className={i === slide ? 'carousel-item active' : 'carousel-item'}>
          <img src={item.images.large} className="d-block w-100" alt={`${item.title}'s screenshot'`} style={{maxHeight:'100vh',maxWidth: '100vw'}} />
          <div className="carousel-caption d-none d-md-block">
            <h3><span>{item.title}</span></h3>
            <p><span>{$(item.details).text()}</span></p>
          </div>
          <a className="carousel-control-prev" href="#st" role="button" data-slide="prev">
            <i className="fa fa-arrow-left fa-lg"></i>
            {/* <span className="carousel-control-prev-icon"></span> */}
          </a>
          <a className="carousel-control-next" href="#st" role="button" data-slide="next">
            <i className="fa fa-arrow-right fa-lg"></i>
            {/* <span className="carousel-control-next-icon"></span> */}
          </a>
        </div>
      );
    });
    return items;
  }
  componentDidMount() { console.log(this.props); }
  render() {
    return (
      <div id="showtime" className="container-fluid sec">
        <div className="row">
          <div className="col-12" style={{padding: 0}}>
            <div id="st" className="carousel slide" data-ride="carousel">
              <div className="carousel-inner">
                {this.props.loading ? <Loading /> : this.getItems()}
              </div>
              <div className="opts">
                <button type="button" className="btn btn-sec filled btn-sm" onClick={()=>this.props.history.goBack()}><i className="fa fa-arrow-left"></i></button>{' '}
                <Link to="/" className="btn btn-sec filled btn-sm"><i className="fa fa-home"></i></Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ShowTime;



